<template>
  <BaseLayout title="分类" nonavbar>

    <van-tabs class="channel" v-model:active="state.active" type="card" color="#ee0a24" background="transparent"  sticky swipeable animated @change="changeTab" >
      <van-tab v-for="(v,k) in $store.state.channel" :key="k" :title="v.name"></van-tab>
    </van-tabs>

    <van-tree-select class="category" v-model:main-active-index="state.index" :items="state.category" @clickNav="changeNav">
      <template #content>

        <div v-if="typeof state.list.children == 'undefined' || state.list.children.length == 0">
          <van-empty description="暂无分类"></van-empty>
        </div>

        <div v-else-if="hasChildren()">
          <van-cell title="查看全部" is-link :to="'list?cid=' + state.cid" />
          <div v-for="(cat2,j) in state.list.children" :key="j">
            <van-cell :title="cat2.name" is-link arrow-direction="down" :to="'list?cid=' + cat2.cid" />
            <van-grid :column-num="3" :icon-size="36" :border="false">
              <van-grid-item v-for="(cat3,k) in cat2.children" :key="k" :icon="cat3.icon || 'point-gift-o'" :style="{color:color()}" :text="cat3.text" :to="'list?cid=' + cat3.cid" />
            </van-grid>
          </div>
        </div>

        <div v-else>
          <van-cell title=" " value="查看全部" is-link :to="'list?cid=' + state.cid" />
          <van-grid :column-num="3" :icon-size="36" :border="false">
            <van-grid-item v-for="(item,index) in state.list.children" :key="index" :icon="item.icon || 'gift-o'" :style="{color:color()}" :text="item.text" :to="'list?cid=' + item.cid"  />
          </van-grid>
        </div>

      </template>
    </van-tree-select>

  </BaseLayout>
</template>

<style scoped>
.channel {
  padding: 10px;
  background: #FFF url('~@/assets/bg_navbar.jpg') repeat left top;
  border-bottom: solid 1px #EEE;
}
.category {
  min-height: 600px;
}
</style>

<script>
import {reactive} from 'vue'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  props: {
    type: {
      type: String,
      default: "mall",
    },
    id: {
      type: [String, Number],
      default: 1,
    },
  },

  data() {
    let state = {
      category: [],

      active: 0,
      index: 0,

      cid: 0,
      list: {},
    };
    
    for (let i in this.$store.state.channel){
      if (this.$store.state.channel[i].type == this.type){
        state.active = parseInt(i);
      }
    }

    state = reactive(state);
    return {state};
  },

  created(){
    this.changeTab();
  },

  methods: {
    changeTab() {
      this.$router.push("/" + this.$store.state.channel[this.state.active].type + "/category?id=" + this.id);

      this.state.category = [];
      this.state.list = {};
      this.state.cid = 0;
      this.$http.post("/mall/category?mid=" + this.state.active).then((data) => {
        this.state.category = data;
        for (let i in data){
          let id = parseInt(this.id);
          if (data[i].cid == id)
            this.state.index = i;
        }
        this.changeNav();
      });
    },
    changeNav() {
      this.state.list = this.state.category[this.state.index];
      this.state.cid = this.state.list.cid;
      this.$router.push("/" + this.$store.state.channel[this.state.active].type + "/category?id=" + this.state.cid);
    },
    hasChildren(){
      for (let i in this.state.list.children){
        if (typeof this.state.list.children[i] != 'undefined' && this.state.list.children[i].children.length > 0)
          return true;
      }
      return false;
    },
    color() {
      return "rgb(" + _.random(0, 255) + "," + _.random(100, 200) + "," + _.random(100, 200) + ")";
    },
  }
}
</script>
